.modal{position:absolute;top:35%;left:35%;z-index:1;width:500px;background:white;border:1px solid #ccc;transition:1.1s ease-out;box-shadow:-2rem 2rem 2rem rgba(0,0,0,0.2);-webkit-filter:blur(0);filter:blur(0);transform:scale(1);opacity:1;visibility:visible}.modal.off{opacity:0;visibility:hidden;-webkit-filter:blur(8px);filter:blur(8px);transform:scale(0.33);box-shadow:1rem 0 0 rgba(0,0,0,0.2)}@supports (offset-rotate: 0deg){.modal{offset-rotate:0deg;offset-path:path("M 250,100 S -300,500 -700,-200")}.modal.off{offset-distance:100%}}@media (prefers-reduced-motion){.modal{offset-path:none}}.modal h2{border-bottom:1px solid #ccc;padding:1rem;margin:0}.modal .content{padding:1rem}.modal .actions{border-top:1px solid #ccc;background:#eee;padding:0.5rem 1rem}.modal .actions button{border:0;background:#78f89f;border-radius:5px;padding:0.5rem 1rem;font-size:0.8rem;line-height:1}.fieldcontainer{vertical-align:middle}.privateField{resize:none;overflow:auto;background:transparent}

